import { empty, cons, fold, reverse } from "../fable-library.3.0.0-nagareyama-rc-005/List.js";
import { uncurry } from "../fable-library.3.0.0-nagareyama-rc-005/Util.js";
import { toString } from "../fable-library.3.0.0-nagareyama-rc-005/Types.js";
import { mkAttr } from "../Feliz.1.16.0/Interop.fs.js";
import { join } from "../fable-library.3.0.0-nagareyama-rc-005/String.js";

export function Helpers_extractClasses(xs) {
    let list_1;
    list_1 = reverse(xs);
    return fold(uncurry(2, (tupledArg) => {
        const classes = tupledArg[0];
        const props = tupledArg[1];
        return (x) => {
            let v, k;
            const matchValue = x;
            if (v = matchValue[1], (k = matchValue[0], k === "className")) {
                const v_1 = matchValue[1];
                const k_1 = matchValue[0];
                return [cons(toString(v_1), classes), props];
            }
            else {
                return [classes, cons(x, props)];
            }
        };
    }), [empty(), empty()], list_1);
}

export function Helpers_combineClasses(cn, xs) {
    let tupledArg;
    tupledArg = Helpers_extractClasses(xs);
    const classes = tupledArg[0];
    const props = tupledArg[1];
    return cons((mkAttr("className", join(" ", cons(cn, classes)))), props);
}

